
.task {
    position: relative;
    min-height: 40px;
    margin-bottom: 10px;
    // background-color: rgb(255, 51, 51);
    color: #fff;
    line-height: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 10px 8px 12px;
    border-radius: 3px;
    transition: 0.2s ease-out;

    &:hover .task_icon-section {
        opacity: 1;
    }
    
    &:last-child {
        margin-bottom: 0;
    }
    
    .task_name {
        font-size: 1.5rem;
        line-height: 20px;
        padding-right: 10px;
        z-index: 5;

        &.task_name--inactive {
            opacity: 0;
        }

        &::selection {
            background-color: #fff;
            color: #333;
        }
    }
    
    .task_edit {
        position: absolute;
        width: 75%;
        font-size: 1.5rem;
        line-height: 20px;
        color: #fff;
        // padding-right: 10px;
        padding: 0;
        background-color: transparent;
        border: none;
        border-bottom: 1px solid white;
        margin-top: 1px;
        outline: none;
        z-index: 10;
        cursor: text;
        
        &:focus {
            outline: none;
        }
        
        &.task_edit--inactive {
            opacity: 0;
            z-index: 0;
            cursor: default;
        }

        &::selection {
            background-color: #fff;
            color: #333;
        }
    }
    
    &.task--done {
        opacity: 0.3;
    }

    .task_icon-section {
        display: flex;
        align-items: center;
        opacity: 0;
        
        .task-delete-icon {
            font-size: 1.6rem;
            margin-right: 7px;
        }
        
        .task-done-icon {
            font-size: 1.6rem;
        }
    }
}